import 'babel-polyfill'
import Vue from 'vue'
import VueHead from 'vue-head'

import App from './components/app.vue'

import router from './router'

Vue.use(VueHead)

// 创建 vue 实例，并渲染组件
// 渲染app.vue到#app
const app = new Vue({
	el: '#app',
	router,  // 传入router给Vue app
	render: h => h(App)
})


